.nav-bar {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10; }
  .nav-bar .detail-info {
    display: -ms-flexbox;
    display: flex;
    float: right;
    position: absolute;
    top: 1em;
    right: 2em; }
  .nav-bar .input-label {
    color: #fff;
