    line-height: 32px; }
  .nav-bar .select-bar {
    width: 100px;
    color: #fff;
    margin-right: 1.5em; }
    .nav-bar .select-bar:before {
      background-color: #fff; }
    .nav-bar .select-bar:hover:before {
      background-color: #fff !important; }
    .nav-bar .select-bar svg {
      color: #fff; }
  .nav-bar .date-picker {
    display: -ms-flexbox;
    display: flex; }
    .nav-bar .date-picker .picker-label {
      line-height: 32px;
      margin-right: 0.5em; }
    .nav-bar .date-picker .picker > div {
      color: #fff; }
      .nav-bar .date-picker .picker > div:before {
        background-color: #fff; }



